import React, { Component } from 'react'

export class NewsItems extends Component {
    render() {
let {title,description} = this.props
        return (
            <div>
                <div className="card m-3" style={{width: "18rem"}}>
  <img src="https://s.yimg.com/uu/api/res/1.2/eML8pN3348zt5qp7bvpxmw--~B/aD0zMzM7dz01MDA7YXBwaWQ9eXRhY2h5b24-/https://media.zenfs.com/en/usa_today_opinion_532/e4d5a8f44d13e99688f3e9346baa6e63" className="card-img-top" alt="..."/>
  <div className="card-body">
    <h5 className="card-title">{title}</h5>
    <p className="card-text">{description}</p>
    <a href="/" className="btn btn-primary">Go somewhere</a>
  </div>
</div>
            </div>
        )
    }
}



export default NewsItems
